var learnolotlData={id:"learnolotlData",title:"Learnolotl",summary:"language learning flash-card web-app",description:"Learnolotl is a web-app I wrote to study Japanese. When I was first learning Japanese, I wanted to be able to reference all the different versions of a word (English/Romaji/Hiragana/Katakana/Kanji) on a single, hand-written flashcard.  Since a real notecard only has two sides, that was difficult to do.  I set out to solve that single problem and planned out the project with multiple milestones, a style guide, and an in-depth readme.",learned:"Before I started this project, I had little experience working with React.  Throughout, I learned a lot from the official documentation and after my hands-on experience grew, I was able to demonstrate the benefits the framework has over the BackboneJS framework (including memory management concerns, rapid development time, and improved performance).  When my employers were considering new frameworks, I provided demos of the differences and observations which informed our next steps as a team and lead to adopting React for new projects.",loved:"This is my favorite project so far because it is something that I use frequently.  I love that it's fleshed out enough to solve the original problem yet still has a lot of potential to grow.  Going through the exercise of branding it, creating a style guide, and designing a logo was also a real treat and gave me the opportunity to implement more art and design than many of my other projects.",links:[{label:"Demo",url:"https://cflinchbaugh.github.io/Learnolotl/"},{label:"Repo",url:"https://github.com/cflinchbaugh/Learnolotl"}],tags:["React","Redux","Web App","Design","Tested"]},guildData={id:"guildData",title:"Front-end Focused Guild Founder",summary:"founding member of an innovative, collaborative technical mentorship program",description:"I founded and led a bi-weekly guild designed for engineers of all levels to collaboratively define company best practices, explore and adopt new relevant technologies, and develop impactful internal projects.  The guild fostered a thriving environment of engagement, collaboration, and innovation.  Its success not only optimized developer workflows and raised the quality of our codebase(s), but also established a culture of shared ownership and continuous improvement.  This initiative was so impactful that additional guilds for other domains were created using the roadmap and structure I designed, further enhancing company-wide standards and cross-team alignment.",learned:"Founding a guild from scratch provided me with invaluable experience navigating both technical and non-technical challenges.  I focused on identifying needs, specifically I began by understanding the target audience and addressing key pain points in processes, collaboration, and knowledge gaps.  I then fostered engagment by developing strategies to maintain consistent participation via actionable content, promoting open discussion, and recognizing individual contributions.  I promoted collaboration and encouraged teamwork through initiatives that invited engineers to co-lead sessions, present technical ideas, and contribute to impactful decisions.  As the guild became more established I then focused on expanding leadership by defining metrics for success and introducing a panel of three co-leadrs to diversify perspectives, manage workloads more sustainably, and ensure inclusive decision-making.  These experiences refined my leadership skills even further and improved my communication, adapatbility, and insight into crafting a more aligned organization.",loved:"Leading the guild was one of the most fulfilling experiences of my career.  I particularly enjoyed empowering others and witnessing the professional growth of team members as they gained confidence, learned new technologies, and became more well-rounded with full-stack capabilities.  Seeing tangible impact from the guild's efforts, such as improved productivity, more robust and scalable code, better test coverage, and more cross-functional alignment lead to faster project development cycles was especially rewarding.  Creating a legacy as I watched the guild's roadmap inspire the formation of additional specialized guilds, further driving innovation and collaboration, is a true career highlight that will forever remain incredibly rewarding.",links:[],tags:["Leadership","Mentorship","Engagement","Process Improvement","Collaboration","Innovation","Testing"]},didiAndSmilingJohnsData={id:"didiAndSmilingJohnsData",title:"DiDi & Smiling Johns",summary:"small business website (archived)",description:"This was a website I designed and coded for a local business to help unify their branding and improve their web presence.  They were looking to show each professional as an individual with their own specialties and prices, but also provide cohesion to reflect how closely they work together.  To achieve this, I focused on separating each person and providing them a card with their details while directing focus to groupshots and and increased the use of their branding colors to provide unity.",learned:"I have experience working with clients through my profession, but this project allowed me to be the direct point of contact for every step of the process.  I was responsible of the entire project, so I was able to put my full skillset to use and work with the client to identify problems, provide recommendations, create designs, gather feedback, develop the final code, and deploy it.  Standing up a website is not something I regularly do, so there were many opportunities to gain experience throughout the deployment process.",loved:"At this point in my career this was the most rewarding work I had done because I was able to interact with the client, see their joy when the project wrapped, and have signficant impact on their business by driving engagement and increasing their client-base.  The owners reached out to me with compliments they received about the website for years and it was absolutely uplifting, every time.",links:[{label:"Website (Archived)",url:"https://web.archive.org/web/20220308053756/https:/www.didiandsmilingjohns.com/"}],tags:["Website","Design","WordPress"]},codePenData={id:"codePenData",title:"CodePen",summary:"miscellaneous challenges",description:"Unlike the other projects I have listed, CodePen is not a single application, it is a collection of many small demos I have created.  For me, it is a place to experiment with techniques that do not warrant entire projects. I tend to focus on CSS and Animation challenges, but I also enjoy seeing what other developers are creating for inspiration.",learned:"Through CodePen I've gained experience using CSS pseudo-elements like ::after to reduce my reliance on divs and other HTML tags that can sometimes clutter the DOM, improving code clarity and maintainability.  The experience I gained using HTML Canvas, however, was definitely the most eye-opening regarding just how far the web has come.",loved:'My favorite CodePen is the "Daily Profit" which I built using CSS Shapes.  But the best part about CodePen is it allows me to step away from bigger projects and focus on learning just one thing.',links:[{label:"Gallery",url:"https://codepen.io/cflinchbaugh/"}],tags:["CSS","Design","Miscellaneous"]},webRtcData={id:"webRtcData",title:"WebRTC-React",summary:"WebRTC chat",description:"WebRTC chat",learned:"Learned: This project opened my eyes to just how much more my apps could be doing and how far the web has come.  I went through the process of building multiple WebRTC apps, but this is my favorite because I was able to take what I had learned and implement it in the framework with which I'm most comfortable.",loved:"Loved: This project opened my eyes to just how much more my apps could be doing and how far the web has come.  I went through the process of building multiple WebRTC apps, but this is my favorite because I was able to take what I had learned and implement it in the framework with which I'm most comfortable.",links:[{label:"Repo",url:"https://github.com/cflinchbaugh/webrtc-react"}],tags:["WebRTC","on-going","client-side"]},learnolotlNativeData={id:"learnolotlNativeData",title:"Learnolotl Native",summary:"An enhanced version of the Learnolotl web-app written in React Native and tested on Android devices",description:"An enhanced version of the Learnolotl web-app written in React Native and tested on Android devices",learned:"Learned: This project is still in it's early stages, but I'm excited with how far it has already come.  I stripped down the functionality compared to the web-app and instead focused on simplicity while I learn the ins-and-outs of React Native and Android development",loved:"Loved: This project is still in it's early stages, but I'm excited with how far it has already come.  I stripped down the functionality compared to the web-app and instead focused on simplicity while I learn the ins-and-outs of React Native and Android development",links:[{label:"Repo",url:"https://github.com/cflinchbaugh/LearnolotlNative"}],tags:["React Native","Native App","Design"]},fullStackData={id:"fullStackData",title:"Full Stack",summary:"A sample full-stack application with REST capabilities, connected to MongoDB",description:"A sample full-stack application with REST capabilities, connected to MongoDB",learned:"Learned: It was only a matter of time before I started my journey towards full-stack development and with this app fully-functioning, I have a great template to begin building or enhanding other apps.  Configuring and manipulating MongoDB was a challenge because many of the errors thrown during development were obtuse, but after digging deep into StackOverflow and stepping back to consider where things could have been misconfigured, I was able to add all the features I set out to implement.",loved:"Loved: It was only a matter of time before I started my journey towards full-stack development and with this app fully-functioning, I have a great template to begin building or enhanding other apps.  Configuring and manipulating MongoDB was a challenge because many of the errors thrown during development were obtuse, but after digging deep into StackOverflow and stepping back to consider where things could have been misconfigured, I was able to add all the features I set out to implement.",links:[{label:"Repo",url:"https://github.com/cflinchbaugh/fullstack"}],tags:["React","Redux","Client-Side","Server-Side","Full-Stack","MongoDB","Design","Tested"]};/* harmony default export */ __webpack_exports__["a"] = ({codePenData:codePenData,didiAndSmilingJohnsData:didiAndSmilingJohnsData,fullStackData:fullStackData,guildData:guildData,learnolotlData:learnolotlData,learnolotlNativeData:learnolotlNativeData,webRtcData:webRtcData});

